import { Card } from './card.class';
import { AppComponent } from './app.component';

export class Rig {

    cards: Card[] = [];
    isOn: boolean = false;

    public static FromJSON(json: any): Rig {
        var result = new Rig();
        
        result.isOn = json['isOn'];
        result.cards = [];

        for (var i = 0; i < json['cards'].length; i++) {
            result.cards.push(Card.FromJSON(json['cards'][i]));
        }

        return result;
    }

    public IsOn(): boolean {
        return this.isOn;
    }

    public GetPowerClass(): string {
        return this.isOn ? "green" : "red";
    }

    public PowerUsed(): number {
        if (!this.isOn) {
            return 0;
        }

        var power = 0.01; // base power, $0.01/sec

        for (var i = 0; i < this.cards.length; i++) {
            power += this.cards[i].PowerUsed();
        }

        return power;
    }

    public ClickereumGenerated(): number {
        if (!this.isOn) {
            return 0;
        }

        var generated = 0;

        for (var i = 0; i < this.cards.length; i++) {
            generated += this.cards[i].ClickereumGenerated();
        }

        // apply difficulty to mining
        generated *= 100;
        generated /= AppComponent.instance.GetMiningDifficulty();

        return generated;
    }

    public TogglePower(): void {
        this.isOn = !this.isOn;
    }

    public CanInstallCards(): boolean {
        return (6 - this.cards.length) > 0;
    }
    
    public InstallPooForce1(): void {
        var card = new Card("PooForce 1", 0.03, 1); // uses $0.03/second, generates 1 clickereum per second
        this.cards.push(card);
    }

    public InstallPooForce2(): void {
        var card = new Card("PooForce 2", 0.08, 2.5); // uses $0.08/second, generates 2.5 clickereum per second
        this.cards.push(card);
    }
    
    public InstallPooForce3(): void {
        var card = new Card("PooForce 3", 0.75, 25); // uses $0.75/second, generates 25 clickereum per second
        this.cards.push(card);
    }
    
    public InstallPooForce4(): void {
        var card = new Card("PooForce 4", 500.00, 100000); // uses $500.00/second, generates 1,000,000 clickereum per second
        this.cards.push(card);
    }
}



