export class Card {
    private name: string = 'Unknown Card';
    private power: number = 0;
    private clickereum: number = 0;

    constructor(name: string, power: number, clickereum: number) {
        this.name = name;
        this.power = power;
        this.clickereum = clickereum;
    }

    public static FromJSON(json: any): Card {
        var result: Card = new Card(json['name'], json['power'], json['clickereum']);
        return result;
    }

    public PowerUsed(): number {
        return this.power;
    }

    public ClickereumGenerated(): number {
        return this.clickereum;
    }

    public Name(): string {
        return this.name;
    }
}


